.Register {
  text-align: center;
}

.Register-logo {
  animation: Register-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.Register-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Register-link {
  color: #61dafb;
}

@keyframes Register-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.background {
  background-color: #258ea6;
}

/* Colors */
/*
  dark blue: #476A6F
  light blue: #258EA6
  dark green: #519E8A
  medium green: #7EB09B
  light green: #A3C4BC
  */

.wrapper {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  background-image: url(../../../src/images/parkourRegister.jpg);
}

.form-wrapper {
  width: 400px;
  padding: 20px 40px;
  border-radius: 10px;
  box-shadow: 0px 10px 50px #555;
  background: rgba(0, 0, 0, 0.8) !important;
}

.formStyling {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.h1Style {
  text-align: center;
  width: 100%;
  color: #111;
  font-weight: lighter;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

label {
  font-size: 0.8em;
  margin-bottom: 0.25em;
  color: #222;
  font-weight: lighter;
}

.firstName input {
  margin: 15px 10px;
  border-radius: 5px;
  outline: none;
  border: 1px solid #cfcfcf;
}

.lastName input {
  margin: 15px 10px;
  border-radius: 5px;
  outline: none;
  border: 1px solid #cfcfcf;
}

.email input {
  margin: 15px 10px;
  border-radius: 5px;
  outline: none;
  border: 1px solid #cfcfcf;
}

.password input {
  margin: 15px 10px;
  border-radius: 5px;
  outline: none;
  border: 1px solid #cfcfcf;
}

input::placeholder {
  font-size: 1.2em;
  font-weight: lighter;
  color: #999;
}

input.error {
  border: 1px solid red;
}

.errorMessage {
  color: red;
  font-size: 0.75em;
  display: relative;
}

.firstName {
  margin-right: 1%;
}

.lastName {
  margin-left: 1%;
}

.firstName,
.lastName,
.email,
.password,
.phone {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

.firstName,
.lastName {
  width: 49%;
}

.email,
.password,
.phone {
  width: 100%;
}

.createAccount {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.createAccount button {
  background-color: #519e8a;
  color: #fff;
  border: 2px solid #fff;
  width: 100%;
  margin-top: 1em;
  padding: 8px 0px;
  font-size: 1em;
  font-weight: lighter;
  justify-content: center;
  letter-spacing: 1px;
  margin-bottom: 0.25em;
}

.createAccount button:hover {
  color: #519e8a;
  background-color: #fff;
  border: 2px solid #519e8a;
}

.createAccount small {
  color: #999;
  font-weight: lighter;
}

.createAccount button {
  text-align: center !important;
}

input[type="checkbox"] {
  display: inline-block;
  vertical-align: middle;
}

.styling {
  font-weight: bold;
}

.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.5);
}
.popup\_inner {
  position: absolute;
  left: 25%;
  right: 25%;
  top: 25%;
  bottom: 25%;
  margin: auto;
  border-radius: 20px;
  background: white;
}

input[type="text"]#fn,
input[type="text"]#ln,
input[type="password"]#pass,
input[type="password"]#cpass,
input[type="email"]#email {
  background-color: rgba(0, 0, 0, 0) !important;
  border: 0 !important;
  outline: 0 !important;
  border-bottom: 2px solid rgb(66, 66, 66, 0.4) !important;
  font-size: 19px !important;
  font-weight: bold;
  /* color: rgb(121, 120, 120) !important; */
  color: #fff !important;
  
}

input[type="text"]#fn:focus,
input[type="text"]#ln:focus,
input[type="password"]#pass:focus,
input[type="password"]#cpass:focus,
input[type="email"]#email:focus {
  border-bottom: 2px solid red !important;
}

.submit-spce-reg {
  margin-top: 30px !important;
}

.help-links-login {
  color: white !important;
  font-size: 15px;
}

.help-links-login:hover {
  color: rgb(247, 78, 78) !important;
}
