/**HomePage**/
html {
  height: 100%;
  overflow: hidden;
}

body {
  color: black;
  margin: 0;
  padding: 0;
  perspective: 1px;
  transform-style: preserve-3d;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
  font-family: "Luna";
}

header {
  box-sizing: border-box;
  min-height: 100vh;
  padding: 3vw 0 5vw;
  position: relative;
  transform-style: inherit;
  width: 100vw;
}
header h1 {
  margin-top: -100px;
}
header,
header:before {
  background: 50% 50% / cover;
}
header::before {
  bottom: 0;
  content: "";
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  display: block;
  background-image: url(../src/images/parkourHome2.jpg);
  background-size: cover;
  transform-origin: center center 0;
  transform: translateZ(-1px) scale(2);
  z-index: -1;
  min-height: 100vh;
}

header * {
  font-weight: normal;
  letter-spacing: 0.2em;
  text-align: center;
  margin: 0;
  padding: 1em 0;
}

img {
  max-width: 100%;
  max-height: 100%;
}

.align-to-left {
  text-align: left;
}
.main-img2 {
  background: url("../src/images/parkourHome.jpg");
  /* Set rules to fill background */
  min-height: 570px;
  min-width: 1024px;

  /* Set up proportionate scaling */
  width: 100%;
  height: 50%;

  /* Set up positioning */

  top: 0;
  left: 0;

  animation: animateHeroimage 2s;
}

@keyframes animateHeroimage {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes animateProgramimage {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes animateHeroimageText {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

